import React from 'react'

function getIconName(inclusion){
    switch(inclusion){
        case "Flights":
            return "flight_takeoff";
        case "Hotels":
            return "hotel";
        case "Meals":
            return "restaurant_menu";
        case "Sightseing":
            return "camera_alt"
        case "Bus":
            return "directions_bus"
        case "Transport":
            return "commute"        
        default:
            return "brightness_7"                
    }
}

function Features({data}){

    return (
        <div id="package-features" className="z-depth-1">
            <h6 className="pac-display-text">Features</h6>
            <hr />
            <div className="inclusions">
                {
                    data.map((feature,index)=>{
                        return (
                            <div className="inclusion" key={`${index}`}>
                                <span><i className="material-icons">{ getIconName(feature) }</i></span>
                                <p>{ feature }</p>
                            </div>                                       
                        )
                    })
                }
            </div>            
        </div>
    )
}

export default Features;