import React, { Component } from 'react'
import '../../css/package.css'
import DurationComponent from './DurationComponent'
import SimilarPackages from './SimilarPackages'
import Overview from './Overview'
import Features from './Features'
import Inclusions from './Inclusions'
import Itenary from './Itenary'
import axios from 'axios'

let baseUrl = "/single_pac/"

let loading = (
    <div className="loader-container">
      <div className="loader">
      
      </div>
    </div>
)

function changeTitle(title){
    document.title = title;
}

class PackageWrapper extends Component {

    state = {
        packageData: null
    }

    componentDidMount(){
        const { packagesId, packageName } = this.props.match.params;
        console.log(packagesId,packageName);

        changeTitle(`${packagesId.toUpperCase()} Tour : ${packageName.toUpperCase()}`)

        axios.get(`${baseUrl}/${packagesId}/${packageName}`).then((res)=>{
            // console.log(res);
            this.setState({
                packageData: res.data
            })
        })
     
    }


    handleTOurButtonClick = ()=>{
        const { packageName } = this.props.match.params;
        let text = `Need Some Assistence on "${packageName}" Tour Package`
        window.open(`https://api.whatsapp.com/send?phone=+919818040401&text=${text}`,"_blank")
    }

    render(){
        if(this.state.packageData !== null){
            let { name, imageUrls, days, night, scheduleString, price, priceType, overview, features, inclusions, itenary } = this.state.packageData;
            return (
                <div style={{marginBottom: '150px'}}>
                    <header id="package-header" style={{ backgroundImage: `url('/images/${imageUrls[1]}')` }}>
                        <h3>{ name }</h3>
                    </header>

                    <section id="package-body" className="container">
                        <div className="row">
                            <div className="col s12 m8 package-content">
                                <DurationComponent days={days} nights={night} scheduleString={scheduleString} price={price} priceType={priceType} />
                                <Overview data={overview} />
                                <Features data={features} />
                                <Itenary data={itenary} />
                                <Inclusions data={inclusions} />
                            </div>

                            <div className="col s12 m4 similar-package">
                                <SimilarPackages name={name} packagesId={this.props.match.params.packagesId} />
                            </div>
                        </div>
                    </section>

                    <button onClick={this.handleTOurButtonClick} className="tour-button">Plan My Trip</button>
                </div>
            )
        }else {
            return loading
        }
    }
}

export default PackageWrapper