import React, { useEffect, useState } from 'react'
import axios from 'axios'

let packageBaseUrl = '/package'
function Package({data,packagesId}){

    let link = `${packageBaseUrl}/${packagesId}/${data.name}`

    return (
        <a href={link} target="_blank">
        <div className="sim-pac">
            <img src={`/images/${data.imageUrls[0]}`} />
            <h6 className="secondary-text" style={{fontWeight:'600',fontSize:'20px', marginTop: '5px', textTransform:'capitalize'}}>{ data.name }</h6>
            <p>{ `${data.night}N/${data.days}D` }</p>
        </div>
        </a>
    )
}

let baseUrl = "/pac/"

function SimilarPackages(props){

    let [ similarItems, setSimilarItems ] = useState(null);

    useEffect(()=>{
        let { packagesId } = props;
        console.log(props);
        axios.get(`${baseUrl}/${packagesId}`).then((res)=>{
            // console.log(res);
            setSimilarItems(res.data);
        })        

    },[])

    return (
        <div id="similar-packages" className="z-depth-1">
            <h6 className="secondary-text" style={{fontWeight:'bold'}}>Similar Tours</h6>
            <hr />
            <div className="all-sim">
                {
                    similarItems != null ? similarItems.length == 1 ? (
                        <h6 className="center">No Similar Items Found</h6>
                    ) : 
                    similarItems.map((item,index)=>{
                        if(item.name != props.name) return <Package packagesId={props.packagesId} data={item} key={`${index}`} />
                        else return (<div key={`${index}`}></div>)
                    }) : (
                        <h6 className="center">Loading...</h6>
                    )
                }
            </div>
            <br />
        </div>
    )
}

export default SimilarPackages