import React from 'react'


function Box({data}){
    return (
        <div className="box">
            <h6 className="inner-text">{ data }</h6>
            <div className="line"></div>
        </div>
    )
}

function DataBox({title,items,special}){
    let classes = special === null ? "box-container" : `box-container ${special}`
    return (
        <div className={classes}>
            <h6 className="secondary-text">{ title }</h6>
            <div className="boxes z-depth-1">
                {
                    items.map((item,index)=> {
                        return <Box data={item} key={index} />
                    })
                }
            </div>
        </div>
    )
}

function DurationComponent({days,nights,scheduleString,price,priceType}){

    let scheduleArray = scheduleString.split('=>');

    return (
        <div id="package-duration" className="z-depth-1">
            <div className="package-datas">
                <DataBox title={"Duration"}  items={[`${nights} Nights`,`${days} Days`]} />
                <DataBox title={"Price"} items={[`â‚¹ ${price} ${priceType}`]} />
            </div>
            <DataBox title={"Schedule"} items={scheduleArray} special={"special-box"} />
            <br />
        </div>
    )
}


export default DurationComponent;